<template>
  <v-container class="mt-10">
    <div class="authprev">
      <v-card class="pa-3">
        <h3>new account</h3>
        <v-divider class="ma-3"></v-divider>
        <v-form ref="signup" v-model="validator">
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="username"
              outlined
              v-model="username"
              dense
              :rules="usernameRules"
              rounded
              prepend-inner-icon="mdi-account-outline"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="email"
              outlined
              v-model="email"
              dense
              :rules="emailRules"
              rounded
              prepend-inner-icon="mdi-email-outline"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="password"
              outlined
              dense
              rounded
              :rules="passwordRules"
              v-model="password"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
              hint="At least 8 characters"
              counter
              prepend-inner-icon="mdi-key-outline"
              @click:append="show1 = !show1"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0 text-center">
            <v-btn
              class="col-md-12"
              dense
              rounded
              color="success"
              @click="handleSubmit"
              :dark="!loading"
              elevation="3"
              :loading="loading"
            >
              <v-icon>mdi-plus-outline</v-icon>create now
            </v-btn>
          </v-col>
        </v-form>
        <v-divider class="ma-3"></v-divider>
        <router-link to="/login">
          <p>login you account</p>
        </router-link>
      </v-card>
    </div>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      password: "",
      email: "",
      username: "",
      validator: true,
      loading: false,
    };
  },
  computed: {
    usernameRules() {
      return [
        (v) => !!v || "error  in username is required",
        (v) => this.$pattern.username.test(v) || "error  username not valid",
      ];
    },
    emailRules() {
      return [
        (v) => !!v || "error  in email is required",
        (v) => this.$pattern.email.test(v) || "error  email not valid",
      ];
    },
    passwordRules() {
      return [
        (v) => !!v || "error  in password is required",
        (v) => this.$pattern.password.test(v) || "error  password not valid",
      ];
    },
    valid() {
      return this.$refs.signup.validate();
    },
  },
  methods: {
    handleSubmit() {
      if (this.valid) {
        this.loading = true;
        this.$store
          .dispatch("signUp", {
            username_u: this.username,
            password: this.password,
            email: this.email,
          })
          .then((resul) => {
            this.loading = false;
            // resul.status === 200 &&
            if (resul.data.code == 200) {
              ///////login
              this.loading = true;

              this.$store
                .dispatch("signIn", {
                  username_u: this.username,
                  password: this.password,
                })
                .then(async (resul) => {
                  this.loading = false;

                  if (resul.status === 200 && resul.data.code === 200) {
                    if (this.$route.query.nextPath) {
                      this.$router
                        .push({
                          name: this.$route.query.nextPath,
                          params: this.$route.params,
                        })
                        .then(null, (err) => {
                          this.$notify({
                            group: "errors",
                            type: "success",
                            title: "error",
                            text: "successfully",
                          });
                          this.$router.push({ name: "NotFound" });
                          return err;
                        });
                    } else this.$router.push({ name: "Home" });
                  }
                })
                .catch((err) => {
                  this.loading = false;
                  this.$notify({
                    group: "errors",
                    type: "success",
                    title: "error",
                    text: err.message,
                  });
                  return err;
                });

              //////fin login
            } else {
              alert("sks");
              this.$notify({
                group: "errors",
                type: "error",
                title: "error",
                text: "error",
              });
            }
          })
          .catch((err) => {
            alert(err.message);
            this.$notify({
              group: "errors",
              type: "error",
              title: "error",
              text: err.message,
            });

            this.loading = false;
          });
      }
    },
  },
};
</script>
